
.grid-container {
  display: grid;
  grid-template-columns: 1fr 800px 50px 1fr;
  grid-template-rows: 30px 1fr 40px 40px 10fr 1fr 1fr;
  gap: 10px 1px;
  grid-template-areas:
    ". . . ."
    ". logo . ."
    ". uploadArea . ."
    ". widthInfo . ."
    ". imgParent lengthInfo ."
    ". total . ."
    ". . . .";

    background: -webkit-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
    background: -moz-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
    background: -o-linear-gradient(top left, #F3F6F1 0%, #D9DED6 100%);
    background: linear-gradient(to bottom right, #F3F6F1 0%, #D9DED6 100%);
    font-family: 'Roboto', sans-serif;
    color: #31345A;
}

.logo { grid-area: logo; }


.uploadArea {
  display: grid;
  grid-template-columns: 4fr .5fr .5fr .5fr;
  grid-template-rows: .5fr;
  gap: 1px 20px;
  grid-template-areas:
    "upload input inc dec";
  grid-area: uploadArea;
  justify-content: center;
}

.inc { 
  grid-area: inc;   
  display: grid; 
  justify-content: right; 
}
.input { 
  grid-area: input;
  display: grid; 
  justify-content: right; 
}

.toggle { grid-area: toggle; }

.dec { 
  grid-area: dec;
  display: grid; 
  justify-content: right; 
}

.upload1 { 
  grid-area: upload;
  display: grid; 
  justify-content: left; 
}

.total { 
  display: grid;
  grid-area: total;
  justify-content: right; 
}

.widthInfo { 
  display: grid; 
  grid-area: widthInfo;  
  justify-content: right;
  align-content: end; 
}

.lengthInfo { 
  display: grid;
  grid-template-columns: 1fr;
  /* grid-template-rows: 1fr; */
  gap: 1px 1px;
  grid-template-areas:
    "length";
  grid-area: lengthInfo;
  justify-content: center;
}
.link{
  color:#EB2F7D;
  text-decoration: none;
}
button{
  color:#4051B5;
  background-color: purple;
}

.length {
   display: grid;
   grid-area: length;
   justify-content: center;
   }
   #rotate-text{
    writing-mode: vertical-rl;
    text-orientation: mixed;
    justify-content: center;
   }

.imgParent {
  grid-area: imgParent;
  width: 800px;
}
canvas,img {
  display: block;
  width: 100%;
  height: auto;
}


